<template>
  <CardSection title="Experiencia" icon="mdi-account-star">
    <h3 class="px-8 mb-0 text-center text-sm-left">Selecciona una experiencia para conocer más</h3>

    <v-slide-group v-model="selected" class="py-4" show-arrows center-active>
      <v-slide-item v-for="(item, index) in data" :key="index" v-slot="{ active, toggle }">
        <v-card flat class="mx-2" :color="active ?  activeItemClass : inactiveItemClass" @click="toggle" height="200" width="200">
          <v-container fluid class="fill-height">
            <v-row class="fill-height" align="center" justify="center">
              <div class="text-center px-5">
                <v-icon class="mb-2" large :color="active ? activeIconClass : inactiveIconClass">mdi-office-building-marker</v-icon>
                <h3 :class="active ? activeTextClass : inactiveTextClass">{{ item.business.name }}</h3>
              </div>
            </v-row>
          </v-container>
        </v-card>
      </v-slide-item>
    </v-slide-group>

    <v-expand-transition>
      <v-sheet v-if="itemSelected != null" tile class="px-8">
        <v-row align="center" justify="center">
          <v-col>
            <v-list-item three-line class="pa-0">
              <v-list-item-content class="pt-0">
                <v-list-item-title class="text-wrap">
                  <h2>{{ itemSelected.role }}</h2>
                </v-list-item-title>
                <v-list-item-subtitle>
                  <h3>{{ itemSelected.business.name }} - {{ itemSelected.type }}</h3>
                </v-list-item-subtitle>
                <v-list-item-subtitle>
                  {{ itemSelected.date.start }} - {{ itemSelected.date.end }}
                </v-list-item-subtitle>
                <v-list-item-subtitle>
                  {{ itemSelected.business.location }}
                </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
            <v-list-item class="pa-0" v-if="itemSelected.description.length > 0">
              <ul>
                <li v-for="(item, index) in itemSelected.description" :key="index">
                  {{item}}
                </li>
              </ul>
            </v-list-item>
            <v-list-item class="px-0 pt-2" v-if="itemSelected.project">
              <v-btn color="primary" rounded depressed :block="$vuetify.breakpoint.xsOnly" :outlined="$vuetify.theme.dark" :to="`/projects/${itemSelected.project}`">Ver Proyecto</v-btn>
            </v-list-item>
          </v-col>
        </v-row>
      </v-sheet>
    </v-expand-transition>

  </CardSection>

</template>

<script>
import CardSection from './CardSection.vue'

import experiences from '../assets/experiences'

export default {
  components: {
    CardSection
  },
  data: () => ({
    selected: null,
    data: experiences
  }),
  methods: {

  },
  computed: {
    itemSelected() {
      return this.selected != null ? this.data[this.selected] : null;
    },
    activeItemClass() {
      return this.$vuetify.theme.dark ? 'primary' : 'primary';
    },
    inactiveItemClass() {
      return this.$vuetify.theme.dark ? 'grey darken-1' : 'grey lighten-3';
    },
    activeIconClass() {
      return this.$vuetify.theme.dark ? 'white' : 'white';
    },
    inactiveIconClass() {
      return this.$vuetify.theme.dark ? 'white' : 'grey darken-1';
    },
    activeTextClass() {
      return this.$vuetify.theme.dark ? 'white--text' : 'white--text';
    },
    inactiveTextClass() {
      return this.$vuetify.theme.dark ? 'white--text' : 'grey--text text--darken-1';
    }
  }
}
</script>

<style lang="scss" scoped>
@import "~vuetify/src/styles/settings/_variables";

.image {
  width: 100%;
  height: 100%;
}

@media #{map-get($display-breakpoints, "xs-only")} {
  .experience-card {
    width: 100%;
    height: 200px;
  }
}
</style>