<template>
  <v-row no-gutters class="banner-row">
    <v-col style="position:relative;">
      <div class="banner-image">
      </div>
      <v-container class="fill-height px-0 py-15">
        <v-row no-gutters style="z-index: 1;">
          <v-col>
            <!-- <v-card> -->
            <v-container fluid class="fill-height pa-0">
              <v-row align="center" justify="center" no-gutters>

                <template v-if="$vuetify.breakpoint.mdAndUp">
                  <v-col cols="12" md="5">
                    <div class="profile-image">
                      <img style="opacity:0" width="100%" height="auto" @load="animateProfileImage" src="@/assets/profile5.png" alt="profile-image" />
                    </div>
                  </v-col>

                  <v-col cols="12" md="7" class="pl-md-6">
                    <div ref="greeting" class="d-inline-block overflow-hidden text-no-wrap text-left pb-5" style="opacity:0">
                      <h1 class="text-hello">Hola,</h1>
                      <h1 class="text-name">Soy César Rodríguez.</h1>
                      <div class="mt-5">
                        <v-btn color="primary" rounded @click="readAboutMe">Leer Sobre Mi</v-btn>
                      </div>
                    </div>
                  </v-col>
                </template>

                <template v-else>
                  <v-col cols="12">
                    <div class="text-center">
                      <h1 class="text-hello" ref="hello">¡Hola!</h1>
                      <h1 class="text-name" ref="name">Soy César Rodríguez.</h1>
                      <div class="mt-5" ref="button">
                        <v-btn color="primary" rounded @click="readAboutMe">Leer Sobre Mi</v-btn>
                      </div>
                    </div>
                  </v-col>

                  <v-col cols="12" class="mt-15 text-center" ref="chevron">
                    <v-btn icon @click="readAboutMe" aria-label="leer mas">
                      <v-icon large>mdi-chevron-down</v-icon>
                    </v-btn>
                  </v-col>
                </template>

              </v-row>

            </v-container>
            <!-- </v-card> -->
          </v-col>
        </v-row>

      </v-container>
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: 'BannerSection',
  data: () => ({
    profileImageLoaded: false
  }),
  mounted() {
    if (!this.$vuetify.breakpoint.mdAndUp) {
      this.animateMobileHeader();
    }
  },
  methods: {
    readAboutMe() {
      this.$vuetify.goTo('#aboutme', { duration: 1000, easing: 'easeOutCubic' });
    },
    animateProfileImage(event) {
      this.$anime
        .timeline()
        .add({ targets: event.target, opacity: [0, 1], easing: 'easeOutQuad', duration: 1000 })
        .add({ targets: event.target, translateX: ['50%', '0%'], easing: 'easeOutQuad', duration: 1000 })
        .add({ targets: this.$refs.greeting, width: ['0%', '100%'], opacity: [0, 1], easing: 'easeOutQuad', duration: 1000 }, '-=800')
    },
    animateMobileHeader() {
      this.$anime
        .timeline()
        .add({ targets: this.$refs.hello, opacity: [0, 1], easing: 'easeOutQuad', duration: 1000, delay: 500 })
        .add({ targets: this.$refs.hello, translateY: ['50%', '0%'], easing: 'easeOutQuad', duration: 900 }, '-=10')
        .add({ targets: this.$refs.name, opacity: [0, 1], translateY: ['-40%', '0%'], easing: 'easeOutQuad', }, '-=900')
        .add({ targets: this.$refs.button, opacity: [0, 1], translateY: ['-100%', '0%'], easing: 'easeOutQuad', }, '-=850')
        .add({ targets: this.$refs.chevron, opacity: [0, 1], translateY: ['-100%', '0%'], easing: 'easeOutQuad', }, '-=800');
    }
  },
  watch: {
    '$vuetify.breakpoint.mdAndUp'(value) {
      if (!value) {
        this.$nextTick(() => {
          this.animateMobileHeader();
        });
      }
    }
  }
}
</script>

<style lang="scss">
@import "~vuetify/src/styles/settings";

.banner-row {
  height: calc(100vh - 70px);
  overflow: hidden;
}

.profile-image {

  z-index: -1;
  position: absolute;
  top: 0;
  // left: 0;

  // margin-left: auto;
  max-width: 600px;

  img {
    width: 100%;
    vertical-align: bottom;
  }
}

.text-hello {
  color: transparent;
  font-size: 7em;
  line-height: 1;
}

.text-name {
  font-size: 3em;
}

@media #{map-get($display-breakpoints, "xs-only")} {
  .text-hello {
    -webkit-text-stroke-width: 0.5px;
    font-size: 4em;
  }

  .text-name {
    -webkit-text-stroke-width: 0.5px;
    font-size: 1.8em;
  }
}

@media #{map-get($display-breakpoints, "md-only")} {
  .text-hello {
    -webkit-text-stroke-width: 0.5px;
    font-size: 6.5em;
  }

  .text-name {
    -webkit-text-stroke-width: 0.5px;
    font-size: 2.5em;
  }

  .profile-image {
    top: 50%;
    left: 4%;
    transform: translateY(-50%);
    // width: 70vh;
    max-width: 500px;
  }
}

@media #{map-get($display-breakpoints, "lg-only")} {
  .text-hello {
    -webkit-text-stroke-width: 0.5px;
    font-size: 8em;
  }

  .text-name {
    -webkit-text-stroke-width: 0.5px;
    font-size: 3em;
  }

  .profile-image {
    top: 50%;
    left: 25%;
    transform: translate(-50%, -50%);
    width: 70vh;
    max-width: 800px;
  }
}

@media #{map-get($display-breakpoints, "xl-only")} {
  .text-hello {
    -webkit-text-stroke-width: 0.5px;
    font-size: 10em;
  }

  .text-name {
    -webkit-text-stroke-width: 0.5px;
    font-size: 4em;
  }

  .profile-image {
    top: 50%;
    transform: translateY(-50%);
    width: 70vh;
    max-width: 800px;
  }
}

.banner-image {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: repeat;
  background-size: contain;
  // opacity: 0.5;
  z-index: 0;
  transition: background-color 0.2s ease;
}

.theme--light {
  .text-hello {
    -webkit-text-stroke: 2px map-deep-get($material-light, "text", "primary");
  }

  .text-name {
    color: map-deep-get($material-light, "text", "primary");
  }

  .banner-image {
    background-image: url(@/assets/pattern-light.png);
    background-color: map-deep-get($material-light, "background");
  }
}

.theme--dark {
  .text-hello {
    -webkit-text-stroke: 1px map-deep-get($material-dark, "text", "primary");
  }

  .text-name {
    color: map-deep-get($material-dark, "text", "primary");
  }

  .banner-image {
    background-image: url(@/assets/pattern-dark.png);
    // background-color: black;
    // background-color: map-deep-get($material-dark, "background");
  }
}
</style>